.cats-images {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    position: absolute;
    width: 100%;
    -webkit-transform: translateX(-64%);
        -ms-transform: translateX(-64%);
            transform: translateX(-64%);
    margin-bottom: 16px;
}

.cats-images__error {
    position: absolute;
    width: 200%;
    bottom: -40%;
    color: var(--error-color);
    font-size: 15px;
}
.cats-images__item {
    width: 57%;
    height: 150px;
    border-radius: 5px;
    cursor: pointer;
    border: 3px solid transparent;
}

.cats-images__item--active {
    border: 3px solid var(--active-text-color);
}

.cats-images__item:not(:last-child) {
    margin-right: 16px;
}

@media screen and (max-width: 750px) {
    .cats-images {
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        -ms-flex-pack: center;
            justify-content: center;
        max-width: 250px;
        position: static;
        -webkit-transform: none;
            -ms-transform: none;
                transform: none;
    }

    .cats-images__item {
        width: 200px;
    }

    .cats-images__item:not(:last-child) {
        margin-right: 0;
    }

    .cats-images__error {
        position: static;
        width: auto;
        text-align: center;
        margin: 0;
        margin-top: 5px;
    }
}


